.header {
  background: #FFFFFF;
  box-shadow: 0px 4px 8px rgba(41, 41, 41, 0.15);
  font-size: 26px;
}

.header__inner {
  display: flex;
  flex: 1 1;
  width: 100%;
  min-width: 320px;
  max-width: 1200px;
  padding: 20px;
  margin: 0 auto;
}

.layout {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.layout__content {
  display: flex;
  flex: 1 1;
  width: 100%;
  min-width: 320px;
  max-width: 1200px;
  padding: 0 20px;
  margin: 0 auto;
}

.layout__inner {
  position: relative;
  width: 100%;
}

.sticky__container {
  top: 0px;
  width: 100%;
}
.sticky__container_active {
  position: fixed;
}

.panel {
    padding: 20px;
    background: #ffffff;
}
.item {
    min-height: 100px;
    background: #ffffff;
    box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
    padding: 20px;
    margin-bottom: 20px;
}
@-webkit-keyframes rotate {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes rotate {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.loader {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 50px;
}

.loader__content {
  width: 24px;
  height: 24px;
  background: url(data:image/png;base64,) center no-repeat;
  background-size: 24px 24px;
  -webkit-animation: rotate 1s infinite linear;
          animation: rotate 1s infinite linear;
}

.feed {
    padding-bottom: 50px;
}
.page {
  margin-top: 20px;
  display: flex;
  flex-direction: column;
}

.page__main {
  display: flex;
  margin-top: 20px;
}

.page__sidebar {
  width: 25%;
  flex-shrink: 0;
  padding-right: 20px;
}

.page__content {
  width: 100%;
}

.page__header .sticky__container_active {
  top: 20px;
  max-width: 1200px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}

.page__sidebar .sticky__container_active {
  top: auto;
  bottom: 0px;
  max-width: 300px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans',
    'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #f2f2f2;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

